*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    max-width: 748px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    max-width: 1260px;
  }

  &--form {
    @media (min-width: $tablet-width) {
      max-width: 668px;
    }

    @media (min-width: $desktop-width) {
      max-width: 1260px;
    }
  }
}
